import React from 'react';
import { Link } from "react-scroll"
import resume from "../doc/ponhvathvannCV.pdf"

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Button,
  } from 'reactstrap';

export default class NavBar extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }
    toggle() {
        this.setState({
        isOpen: !this.state.isOpen
        });
    }
    render() {
        return (
        <div>
            <Navbar color="light" light expand="md">
            <NavbarBrand href="https://vbobv.github.io/website/" className="logo">ponhvath-vann</NavbarBrand>
            <NavbarToggler onClick={this.toggle} />
            <Collapse isOpen={this.state.isOpen} navbar>
                <Nav className="ml-auto" navbar>
                <NavItem>
                    <NavLink className="link" style={{cursor: "pointer"}}>
                        <Link 
                            activeClass="active"
                            to = "Project"
                            spy = {true}
                            smooth = {true}
                            offset = {0}
                            duration = {800}
                        >
                        Projects
                        </Link>
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink className="link" style={{cursor: "pointer"}}>
                        <Link 
                            activeClass="active"
                            to = "Contact"
                            spy = {true}
                            smooth = {true}
                            offset = {0}
                            duration = {800}
                        >
                        Contact
                        </Link>
                    </NavLink>
                </NavItem>
                <NavItem>
                    <a href={resume}>
                    <Button outline color="info">Download Résumé</Button>{' '}
                    </a>
                </NavItem>
                </Nav>
            </Collapse>
            </Navbar>
        </div>
        );
    }
}