import React from "react"
import {Container, Row, Col} from 'react-bootstrap'
import cmsGif from "../images/cms.gif"
import travelBuddyGif from "../images/travelBuddy.gif"
import consoleGameGif from "../images/consoleGame.gif"
import websiteGif from "../images/website.gif"
import {Button} from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBroadcastTower } from '@fortawesome/free-solid-svg-icons'
import {faCode} from "@fortawesome/free-solid-svg-icons"

const projectContainerCSS = {
    borderRadius: "20px",
    backgroundColor: "#1F618D",
    margin: "10px",
    padding: "10px"
}

const projectTitleStyle = {
    borderRadius: "20px",
    backgroundColor: "#F6F6F6",
    width: "100%",
    textAlign: "center"
}

const gifWidthProp = {
    width: "80%",
}

const linkStyle ={
    marginTop: "30px",
    color: "black",
    textAlign: "center",
    fontSize: "15px",
}

const live = <FontAwesomeIcon icon={faBroadcastTower}/>
const code = <FontAwesomeIcon icon={faCode}/>



function Project() {
    return (
        <div id="Project">
            <Container >
                <Row>
                    <Col md={12}>
                        <h5 className="sectionHeading"># Projects</h5>
                    </Col>
                </Row>
                <Row noGutters="true">
                    <Col md = {6}>
                        <div style={projectContainerCSS}>
                            <div style={projectTitleStyle}>
                                <h5 style={{padding: "10px 0 10px 0", color: "#1F618D"}}>Customer Membership System</h5>
                            </div>
                            <div style={{textAlign: "center", marginTop: "30px"}}>
                                <img src={cmsGif} alt="Customer Membership System" style={gifWidthProp} />
                            </div>
                            <div style={linkStyle}>
                                <Button outline color="info" href="https://youtu.be/05hQS6Ez9WU" style = {{marginLeft: '4px'}}>{live} Watch Demo</Button>{' '}
                                <span style = {{margin: '0'}}></span>
                                <Button outline color="info" href="https://github.com/vBoBv/Java-Project">{code} Source Code</Button>{' '}
                            </div>
                            <div>
                                <h6 style={{marginLeft: "30px", marginTop: "30px", color: "white"}}>Description:</h6>
                                <ul style={{marginLeft: "30px", color: "white"}}>
                                    <li>GUI software developed with Java programming language</li>
                                    <li>Utilized Object-Oriented Programming</li>
                                    <li>Encapsulation, File Handlings and Exception Handlings</li>
                                    <li>Implement ArrayList and String Tokenizer to read and write data to csv files</li>
                                    <li className="hide"></li>
                                    <li className="hide"></li>
                    
                                </ul>
                            </div>
                        </div>
                    </Col>
                    <Col md = {6}>
                        <div style={projectContainerCSS}>
                            <div style={projectTitleStyle}>
                                <h5 style={{padding: "10px 0 10px 0", color: "#1F618D"}}>Escape Game</h5>
                            </div>
                            <div style={{textAlign: "center", marginTop: "30px"}}>
                                <img src={consoleGameGif} alt="Customer Membership System" style={gifWidthProp} />
                            </div>
                            <div style={linkStyle}>
                                <Button outline color="info" href="https://youtu.be/kIYd6mz61B4" style = {{marginLeft: '4px'}}>{live} Watch Demo</Button>{' '}
                                <span style = {{margin: '0'}}></span>
                                <Button outline color="info" href="https://github.com/vBoBv/Python-Project">{code} Source Code</Button>{' '}
                            </div>
                            <div>
                                <h6 style={{marginLeft: "30px", marginTop: "30px", color: "white"}}>Description:</h6>
                                <ul style={{marginLeft: "30px", color: "white"}}>
                                    <li>Using Python programming language</li>
                                    <li>Taking input from the user and using functions to move player from one position to another</li>
                                    <li>A battle-scene using Array to display the health bar between the boss and the player</li>
                                    <li>Using loops to print the fight scene and to process the battle-scene</li>
                                    <li className="hide"></li>
                                </ul>
                            </div>
                        </div>
                    </Col>
                </Row>
                <Row noGutters="true">
                    <Col md = {6}>
                        <div style={projectContainerCSS}>
                            <div style={projectTitleStyle}>
                                <h5 style={{padding: "10px 0 10px 0", color: "#1F618D"}}>Travelling Website (Co-Creator)</h5>
                            </div>
                            <div style={{textAlign: "center", marginTop: "30px"}}>
                                <img src={travelBuddyGif} alt="Customer Membership System" style={gifWidthProp} />
                            </div>
                            <div style={linkStyle}>
                                <Button outline color="info" style = {{marginLeft: '4px'}} href="http://dochyper.unitec.ac.nz/htcs/travelbuddy/Website/index.html" >{live} Live Version</Button>{' '}
                                <span style = {{margin: '0'}}></span>
                                <Button outline color="info" disabled>{code} Source Code</Button>{' '}
                            </div>
                            <div>
                                <h6 style={{marginLeft: "30px", marginTop: "30px", color: "white"}}>Description:</h6>
                                <ul style={{marginLeft: "30px", color: "white"}}>
                                    <li>Using HTML/CSS/JavaScript and Bootstrap</li>
                                    <li>Responsible for the design and the architecture of the coding</li>
                                    <li>Scalable website compatible with Desktop and Tablet screen size</li>
                                </ul>
                            </div>
                        </div>
                    </Col>
                    <Col md = {6}>
                        <div style={projectContainerCSS}>
                            <div style={projectTitleStyle}>
                                <h5 style={{padding: "10px 0 10px 0", color: "#1F618D"}}>Portfolio Website</h5>
                            </div>
                            <div style={{textAlign: "center", marginTop: "30px"}}>
                                <img src={websiteGif} alt="Customer Membership System" style={gifWidthProp} />
                            </div>
                            <div style={linkStyle}>
                                <Button outline color="info" style = {{marginLeft: '4px'}} href="https://vbobv.github.io/website/">{live} Live Version</Button>{' '}
                                <span style = {{margin: '0'}}></span>
                                <Button outline color="info" href="https://github.com/vBoBv/website">{code} Source Code</Button>{' '}
                            </div>
                            <div>
                                <h6 style={{marginLeft: "30px", marginTop: "30px", color: "white"}}>Description:</h6>
                                <ul style={{marginLeft: "30px", color: "white"}}>
                                    <li>Using HTML/CSS/React</li>
                                    <li>Responsive website compatible with Desktop/Tablet/Mobile version</li>
                                    <li className="hide"></li>
                                </ul>
                            </div>
                        </div>
                    </Col>
                </Row>
                <Row noGutters="true">
                    <Col md = {12}>
                        <div style={projectContainerCSS}>
                            <div style={projectTitleStyle}>
                                <h5 style={{padding: "10px 0 10px 0", color: "#1F618D"}}>Banking Application</h5>
                            </div>
                            <div style={{textAlign: "center", marginTop: "30px"}}>
                                <img src={travelBuddyGif} alt="Customer Membership System" style={gifWidthProp} />
                            </div>
                            <div style={linkStyle}>
                                <Button outline color="info" style = {{marginLeft: '4px'}} href="https://youtu.be/NKDQtt5lRng" >{live} Watch Demo</Button>{' '}
                                <span style = {{margin: '0'}}></span>
                                <Button outline color="info" href="https://github.com/vBoBv/Banking-Application">{code} Source Code</Button>{' '}
                            </div>
                            <div>
                                <h6 style={{marginLeft: "30px", marginTop: "30px", color: "white"}}>Description:</h6>
                                <ul style={{marginLeft: "30px", color: "white"}}>
                                    <li>A software application developed using Java programming language</li>
                                    <li>A replica of mobile internet banking but desktop version</li>
                                    <li>User can deposit, withdraw and transfer money from different accounts </li>
                                </ul>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}


export default Project