import React from "react"
import {Container, Row, Col} from 'react-bootstrap'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { fab } from '@fortawesome/free-brands-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

library.add(
    fab
 )
const infoContact = {
    fontSize: "15px",
    textAlign: "center",
    margin: "0"
}
const gitHubIcon = <FontAwesomeIcon icon={["fab","github"]} />
const linkedinIcon = <FontAwesomeIcon icon={["fab","linkedin"]} />
const linkStyle ={
    color: "black",
    textAlign: "center",
    fontSize: "40px",
}
function Contact() {
    return (
        <div style={{marginTop: "30px"}} id="Contact">
            <Container>
                <Row>
                    <Col md={4} xs={12}>
                        <div className="contactUsFormat">
                            <h5 className="sectionHeading" style={{paddingRight: "10px"}}># Contact</h5>
                        </div>
                        
                    </Col>
                    <Col md={8} xs={12}>
                        <div style = {linkStyle}>
                            <span className="pulse">
                            <a 
                                style = {linkStyle} 
                                href="https://github.com/vBoBv"
                                >
                                {gitHubIcon}
                            </a>
                            </span>
                            <span style = {{marginLeft: '10px', marginRight: '10px'}}></span>
                            <span className="pulse">
                                <a 
                                    style = {linkStyle}
                                    href="https://www.linkedin.com/in/ponhvath-vann/">
                                    {linkedinIcon}
                                </a>
                            </span>
                            <p style={infoContact}>vannponhvath@gmail.com</p>
                            <p style={infoContact}>(+64) 0212260622</p>
                        </div>  
                    </Col>
                </Row>
            </Container>
        </div>
        
    )
}

export default Contact