import React from "react"
import {Container, Row, Col} from 'react-bootstrap'

const infoContact = {
    fontSize: "15px",
    textAlign: "center",
    marginBottom: "20px"
}

function Footer() {
    return (
        <div style={{backgroundColor: "#1F618D", marginTop: "30px", color: "white", margin: "20px 0 0 0", padding: "10px 0 20px 0"}}>
            <Container>
                <Row>
                    <Col md={12} xs={12}>
                        <div className="contactUsFormat">
                            <p style={infoContact} >&copy; 2019 By Ponhvath Vann.</p>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
        
    )
}

export default Footer